import * as Sentry from "@sentry/browser";

const LAMBDA_URL =
  "https://xdc9dmik2f.execute-api.us-west-2.amazonaws.com/Prod/submit_order/";

// yeah, not the best thing to do here
export const generateOrderId = () => {
  Math.floor(100000000 + Math.random() * 900000000);
};

export const submitOrderToBackend = (sourceParams, chargeParams) => {
  return new Promise((resolve, reject) => {
    try {
      const xhr = new XMLHttpRequest();
      xhr.open("POST", LAMBDA_URL, true);
      xhr.setRequestHeader("Content-Type", "application/json");
      xhr.responseType = "json";
      xhr.onload = () => {
        const jsonResponse = xhr.response;
        const e = jsonResponse.error;
        if (e) {
          Sentry.captureException(e);
          reject(e);
          return;
        }
        resolve(jsonResponse);
      };
      xhr.onerror = e => {
        reject(e);
      };
      xhr.send(JSON.stringify({ sourceParams, chargeParams }));
    } catch (e) {
      Sentry.captureException(e);
      reject("Unexpected error. Please email support@maplamps.com.");
    }
  });
};
